<template>
<div class="wrapper">
    <div class="index">
        <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
        <div class="left-menu">
            <LeftMenu />
        </div>
        <div class="top-menu">
            <TopMenu />
        </div>
        <div class="aside">
            <Aside />
        </div>
        <div class="news">
            <div class="">
                <div class="categories__wrap">
                    <h1>Наша продукция</h1>
                    <form action="" class="categories__search search">
                        <input type="search" name="q" placeholder="Поиск по товарам" id="search" class="search__first">
                        <input type="submit" value="" class="search__submit">
                    </form>
                </div>
                <Product v-for="name in PRODUCT" :key="name.article" v-bind:product_data="name" />
                <!-- <h2>{{name.name}}</h2>
                <h2>{{name.article}}</h2> -->
            </div>
        </div>
        <div class="footer">
            <Footer />
        </div>
    </div>
</div>
</template>

<script>
    import TopMenu from '@/components/top-menu.vue'
    import LeftMenu from '@/components/left-menu.vue'
    import Aside from '@/components/aside.vue'
    import Footer from '@/components/footer.vue'
    import Product from '@/components/product-item.vue'
    import {
        mapActions,
        mapGetters
    } from 'vuex'
    // import Employee from '@/components/employee.vue'

    export default {
        name: 'index',
        data() {
            return {}
        },
        props: {
            Product: {
                type: Object,
                default () {
                    return {}
                }
            }
        },
        computed: {
            // name() {
            //     return this.$store.getters.findByCateg(this.$route.params.name)
            // },
            ...mapGetters([
                'PRODUCT'
            ])
        },
        methods: {
            ...mapActions([
                'GET_PRODUCT',
            ])
        },
        components: {
            TopMenu,
            LeftMenu,
            Aside,
            Footer,
            Product
            // Employee
        },
        mounted() {
            this.GET_PRODUCT()
                .then((response) => {
                    if (response.data) {
                        console.log('Data arrived')
                    }
                })
                .catch((error) => {
                    console.log(error)
                })
        }
    }
</script>